.App {
   
    margin: 0px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  html, body {
    width: 100%;
    height: 100%;
    background: url('https://www.jotform.com/blog/wp-content/uploads/2018/08/to-do-list-compressor.jpg');
    background-position: center;
    background-size: cover;
   background-repeat: no-repeat;
    background-color: blanchedalmond;
  }
 
  #root {
    width: 100%;
    height: 100%;
  }
  /* Our App Container */
  .container {
    padding-bottom: 80px;
  }
  /* The header */
  .header {
    font-size: 26px;
    font-weight: bold;
    text-align: center;
    padding: 40px;
  }
  /* The note */
  .note {
    color: #adadad;
    padding: 40px;
    margin: 20px;
    background: #fff;
    border: solid 1px #e9e9e9;
    cursor: pointer;
    box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.1);
    -webkit-box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.1);
    -moz-box-shadow: 0px 0px 14px 0px rgba(0,0,0,0.1);
    -webkit-transition: -webkit-transform .2s ease;
    transition: -webkit-transform .2s ease;
    -o-transition: transform .2s ease;
    transition: transform .2s ease;
    transition: transform .2s ease, -webkit-transform .2s ease;
  }
  .note:hover {
    -webkit-transform: scale(1.02);
        -ms-transform: scale(1.02);
            transform: scale(1.02);
  }
  /* The add note button */
  .btn {
    position: fixed;
    z-index: 1;
    right: 150px;
    bottom: 60px;
    width: 80px;
    height: 80px;
    border-radius: 100%;
    text-align: center;
    line-height: 80px;
    font-size: 28px;
    font-weight: bold;
    color: #f1f1f1;
    background: #ca1313;
    cursor: pointer;
    box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.8);
    -webkit-box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.8);
    -moz-box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.8);
    -webkit-transition: -webkit-transform .06s ease;
    transition: -webkit-transform .06s ease;
    -o-transition: transform .06s ease;
    transition: transform .06s ease;
    transition: transform .06s ease, -webkit-transform .06s ease;
  }
  .btn:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
  }
  .btn1 {
    position: fixed;
    z-index: 1;
    right: 60px;
    bottom: 60px;
    width: 80px;
    height: 80px;
    border-radius: 100%;
    text-align: center;
    line-height: 80px;
    font-size: 28px;
    font-weight: bold;
    color: #f1f1f1;
    background: #4e2d4a;
    cursor: pointer;
    box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.8);
    -webkit-box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.8);
    -moz-box-shadow: 0px 0px 11px 0px rgba(0,0,0,0.8);
    -webkit-transition: -webkit-transform .06s ease;
    transition: -webkit-transform .06s ease;
    -o-transition: transform .06s ease;
    transition: transform .06s ease;
    transition: transform .06s ease, -webkit-transform .06s ease;
  }
  .btn1:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
  }
  /* Textinput */
  .textInput {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 80px;
    line-height: 80px;
    padding: 0 20px;
    border: none;
    outline: none;
    background: #e0ccc5;
    font-size: 16px;
    color: #131212;
  }



